<template>
  <div class="consult">
    <van-cell>
      <template slot="title">
        <div class="title">微信支付的姓名异常或修改过如处理?</div>
      </template>
    </van-cell>
    <van-cell>
      <template slot="title">
        <div>
          若你注册微信支付账户后，出现姓名异常（如乱码姓名中
          间空格）或修改过姓名，建议你可以转出账户零钱资金，<span class="logout">点击这里</span>
          注销账户后，重新绑定实名，即可更新账户信息。
        </div>
      </template>
    </van-cell>
    <van-cell title="温馨提示："></van-cell>
    <van-cell>
      <template slot="title">
        <div>1、若你无法操作，或需要人工协助修改，可点击下方 【联系客服】与我们反馈并说明您的情况。</div>
        <div>2、注销后交易记录及零钱明细都会被清空无法恢复， 请谨慎操作。</div>
      </template>
    </van-cell>
    <van-button class="btn">
      <img src="../../assets/img/my/praise.png" alt="点赞">
      <span>已解决</span>
    </van-button>
    <van-button class="btn">
      <img src="../../assets/img/my/trample.png" alt="点赞">
      <span>未解决</span>
    </van-button>
    <div class="bg"></div>
    <van-cell title="未解决还需要"/>
    <van-cell title="联系客服" >
      <div><img src="../../assets/img/my/back.png" alt="返回"></div>
    </van-cell>

  </div>
</template>

<script>
  export default {
    name: "consult",
    data() {
      return {}
    },
    methods: {

    },
  }
</script>

<style scoped lang="less">
  .consult{
    min-height: 100vh;
    .title{
      color: #333;
      font-size: 15px;
      font-weight: bold;
    }
    .logout{
      color: #0090FF;
    }
    .bg{
      height: 10px;
      background-color: #F2F2F2;
    }
    .btn{
      display: inline-block;
      margin: 0 9% 16.5px 9%;
      width: 30%;
      height: 40px;
      font-size: 14px;
      border-radius: 5px;
      line-height: 40px;
      border: 1px solid #DCDCDC;
      img{
        vertical-align: middle;
      }
    }
  }
</style>



